// Marketing Layout - Passthrough layout that lets each page control its own design
// Each marketing sub-page has its own PageHeader and tabs for full control

export default function MarketingLayout({
  children,
}: {
  children: React.ReactNode
}) {
  // Render children directly - each page handles its own header/layout
  // This avoids duplicate headers and allows pages full control over their design
  return <>{children}</>
}
